<template>
  <fragment>
    <body style="background-color: #f1f1f1">
      <div class="bg-overlay" style="">
        <div class="container-fluid p-5">
          <h3 align="center">
            Study Abroad universities for Pakistani Students
          </h3>
          <div class="row mt-4">
            <div class="col-md-4"></div>
            <!-- <div class="col-md-3">
                            <div class="position-relative form-group"><select type="select" id="exampleCustomSelect" name="customSelect" class="custom-select">
                                <option value="">Select Subject</option>
                                <option>Value 1</option>
                                <option>Value 2</option>
                                <option>Value 3</option>
                                <option>Value 4</option>
                                <option>Value 5</option>
                            </select></div>
                        </div>
                        <div class="col-md-3">
                            <div class="position-relative form-group">
                                <select type="select" id="exampleCustomSelect" name="customSelect" class="custom-select">
                                <option value="">Select Level</option>
                                <option>Value 1</option>
                                <option>Value 2</option>
                                <option>Value 3</option>
                                <option>Value 4</option>
                                <option>Value 5</option>
                            </select></div>
                        </div> -->
            <div class="col-md-3">
              <div class="position-relative form-group">
                <select id="CountryOptions" name="select" class="custom-select">
                  <option id="ckh0" value="All">Select Country</option>
                  <option id="ckh1" value="Austria">Austria</option>
                  <option id="ckh2" value="China">China</option>
                  <option id="ckh3" value="UK">UK</option>
                  <option id="ckh4" value="France">France</option>
                  <option id="ckh5" value="Germany">Germany</option>
                  <option id="ckh6" value="Canada">Canada</option>
                </select>
              </div>
            </div>
            <!-- <div class="col-md-3">
                            <div class="position-relative form-group"><input type="text" id="exampleCustomSelect" name="customSelect" class="custom-input-1">
                                </div>
                            </div> -->
          </div>
          <div class="text-center mt-2">
            <button
              v-on:click.prevent="filterSearch"
              class="mb-2 mr-2 btn btn-primary"
            >
              <i class="pe-7s-search"> </i> Search
            </button>
          </div>
        </div>
      </div>
      <Adds :slides="data" />
      <div class="container-fluid" style="padding: 50px">
        <div class="row">
          <div class="col-sm-9">
            <Form />
            <h2 class="text-center"><b> Universities</b></h2>
            <p style="font-size: 16px" class="text-center">
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry.
            </p>

            <TopUniversities :topuni="topuni" />
            <div class="container-fluid mt-3" style="background-color: white">
              <div style="text-align: center" class="pt-2 pb-2">
                <h2 class="mt-5"><b>News And Events</b></h2>
                <p style="font-size: 20px">
                  See the top trending News and book your services now.
                </p>
              </div>
              <section class="pb-5 mt-3">
                <TopNews :topnew="topnew" />
              </section>
            </div>
            <showcomment :commentsshow="commentsshow" />
            <Comment :pagename="pagename" />
          </div>
          <div class="col-sm-3">
            <SideAdds :slides="data" />
          </div>
        </div>
      </div>
    </body>
  </fragment>
</template>
<script>
import config from "@/../config";
//import SliderImg from '@/SliderImages.json'
//import Universities from '@/TopUni.json'
//import Carduni from '@/UniCard.json'
import Adds from "@/components/CorousalSlide";
import SideAdds from "@/components/SideCorousalSlide";
import Form from "@/components/MainForm";
import TopUniversities from "@/components/AllUniversity";
import TopNews from "@/components/TopNews";
import Comment from "@/components/LeaveComment";
import showcomment from "@/components/ShowComment";

export default {
  props: ["data"],
  data() {
    return {
      topnew: [
        {
          title: "Event Title",
          id: "",
          shortdiscription: "Discription",
          countries: "Counteries",
          detail: "",
          imageurl: "",
          email: "",
          dates: "",
          statuscheck: "",
          expiretime: "",
          activatetime: "",
          activatedate: "",
          expiredates: "",
          imageurlbase64:
          'https://abroadstudy.infura-ipfs.io/ipfs/Qme6iTwd7izSNc6xpxNykYGSmsAVGUNgiZBw3FbotE5DJY',
        },
      ],
      topuni: [
        {
          id: "",
          institutename: "University Name",
          email: "Email",
          country: "",
          Detail: "",
          mainpicurlbase64:
          'https://abroadstudy.infura-ipfs.io/ipfs/Qme6iTwd7izSNc6xpxNykYGSmsAVGUNgiZBw3FbotE5DJY',
        },
      ],
      pagename: "University",
      commentsshow: "",
    };
  },
  components: {
    Adds,
    SideAdds,
    Form,
    TopUniversities,
    TopNews,
    Comment,
    showcomment,
  },
  methods: {
    filterSearch() {
      var select = document.getElementById("CountryOptions");
      var countries = select.options[select.selectedIndex].value;
      ////console.log("Country is  : "+ countries);

      this.axios
        .get(config.hostname + "api/instituteinfo/countries/" + countries)
        .then((response) => {
          this.topuni = response.data;
        });
    },
  },
  mounted() {
    this.axios
      .get(config.hostname + "api/eventsinfo/Getall/")
      .then((response) => {
        this.topnew = response.data;
      });

    this.axios
      .get(config.hostname + "api/Commentsinfo/Getall/" + this.pagename)
      .then((response) => {
        this.commentsshow = response.data;
        ////console.log("comments")
        ////console.log(this.commentsshow)
      });

    this.axios
      .get(config.hostname + "api/instituteinfo/Getallinfo/")
      .then((response) => {
        this.topuni = response.data;
      });
    this.axios
      .put(config.hostname + "api/ViewsCounter/MainUniversitypageviewsupdate/")
      .then((response) => {
        response;
        ////console.log(response.data)
      });
  },
};
</script>