<template>
  <div class="bg-overlay">
    <div class="col-sm-6 offset-3 p-5">
      <div align="center"><h2>Latest Scholarship</h2></div>
    </div>
  </div>
  <Adds :slides="data" />
  <div class="container-fluid" style="padding: 50px">
    <div class="row">
      <div v-for="d in datas" :key="d" class="col-sm-9">
        <div class="card" style="padding: 20px">
          <div class="single-blog mb-5 wow fadeInUp animated">
            <img
              :src="d.imageurlbase64"
              style="width: 100%"
              class=""
            />
            <div align="center" class="mt-2">
              <h3>{{ d.title }}</h3>
            </div>
          </div>
          <div align="left">
            <div align="left" class="col-sm-12">
              <p>{{ d.shortdiscription }}</p>
              <p>{{ d.detail }}</p>
            </div>
            <h5 class="mt-3">Services Countries :</h5>
            <div align="left" class="col-sm-12">
              {{ d.countries }}
            </div>
            <h5 class="mt-3">Ratings :</h5>
            <div align="left" class="col-sm-12">
              <form class="rating">
                <input type="radio" id="star5" name="rating" value="5" /><label
                  for="star5"
                  title="Super !!"
                ></label>
                <input type="radio" id="star4" name="rating" value="4" /><label
                  for="star4"
                  title="Geil"
                ></label>
                <input type="radio" id="star3" name="rating" value="3" /><label
                  for="star3"
                  title="Gut"
                ></label>
                <input type="radio" id="star2" name="rating" value="2" /><label
                  for="star2"
                  title="So gut wie"
                ></label>
                <input type="radio" id="star1" name="rating" value="1" /><label
                  for="star1"
                  title="Schlecht"
                ></label>
              </form>
            </div>
            <br /><br />
            <h5>Overall Rewiews</h5>
            <div align="left" class="col-sm-12">
              <span style="color: #1ea1f3; font-size: 17px"
                ><b>{{ d.averagecount }}</b></span
              >
            </div>
            <h5 class="mt-3">Contact Details :</h5>
            <div align="left" class="col-sm-12">
              <div class="row">
                <div class="col-sm-3">
                  <h6>Email :</h6>
                </div>
                <div class="col-sm-9">
                  <label>{{ d.email }}</label>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3">
                  <h6>Dates :</h6>
                </div>
                <div class="col-sm-9">
                  <label>{{ d.dates }}</label>
                </div>
              </div>

              <div class="row">
                <div class="mt-2">
                  <div id="gmap-example"></div>
                </div>
              </div>

              <div class="mt-2">
                <div id="gmap-example"></div>
              </div>
            </div>
            <div align="center" class="col-sm-12 mt-3">
              <button
                v-on:click.prevent="postToController"
                class="btn btn-secondary"
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <SideAdds :slides="data" />
      </div>
    </div>
  </div>
</template>
<script>
import config from "@/../config";
//import SliderImg from '@/SliderImages.json'

import Adds from "@/components/CorousalSlide";
import SideAdds from "@/components/SideCorousalSlide";

export default {
  props: ["data"],
  data() {
    return {
      datas: undefined,
      id: this.$route.params.emailid,
      image: undefined,
      ratingValue: "",
      mainEmail: this.$route.params.mainemail,
    };
  },
  components: {
    Adds,
    SideAdds,
  },

  mounted() {
    if (this.id != "" && this.id != undefined) {
      {
        {
          this.$route.params.mainemail;
        }
      }
      {
        {
          this.$route.params.emailid;
        }
      }
      this.axios
        .get(config.hostname + "api/scholarshipinfo/Getbyid/" + this.id)
        .then((response) => {
          this.datas = response.data;
        });
      this.axios
        .put(
          config.hostname +
            "api/ViewsCounter/ScholarshipSinglepageviewsupdate/",
          {
            Email: this.mainEmail,
          }
        )
        .then((response) => {
          response;
          ////console.log(response.data)
        });
    } else {
      this.$router.push({ path: "/scholarshipPage" });
    }
  },
  methods: {
    getImage(pic) {
      this.image =   pic;
      return this.image;
    },
    starinfo() {
      // var level = document.getElementById("wanttostudy");
      // var educurrentlevelValue = level.options[level.selectedIndex].value
      // //var starget = document.getElementById("wanttostudy").value
      // ////console.log(educurrentlevelValue)
    },
    async postToController() {
      for (
        this.i = 0;
        this.i < document.getElementsByName("rating").length;
        this.i++
      ) {
        if (document.getElementsByName("rating")[this.i].checked == true) {
          this.ratingValue = document.getElementsByName("rating")[this.i].value;
          break;
        }
      }
      //console.log("working");
      await this.axios
        .put(config.hostname + "api/scholarshipinfo/Putreviewbyid/", {
          id: this.id,
          rate: this.ratingValue,
        })
        .then((response) => {
          alert(response.data);
        });
    },
  },
};
</script>