.<template>
  <div class="bg-overlay">
    <div class="col-sm-6 offset-3 p-5">
      <div align="center"><h2>Country Guide</h2></div>
    </div>
  </div>

  <Adds :slides="data" />
  <div class="container-fluid" style="padding: 50px">
    <div class="row">
      <div v-for="d in datas" :key="d" class="col-sm-9">
        <div class="card" style="padding: 20px">
          <div class="single-blog mb-5 wow fadeInUp animated">
            <div align="center" class="mt-2">
              <img
                :src="d.countrylogobase64"
                style="width: 40%"
                class=""
              />
              <h4>{{ d.countryname }}</h4>
            </div>
          </div>
          <div align="left">
            <div align="left" class="col-sm-12">
              <p>{{ d.detail }}</p>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-3">
              <h6>Dates :</h6>
            </div>
            <div class="col-sm-9">
              <label>{{ d.dates }}</label>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <SideAdds :slides="data" />
      </div>
    </div>
  </div>
</template>
<script>
import config from "@/../config";
//import SliderImg from '@/SliderImages.json'
import Adds from "@/components/CorousalSlide";
import SideAdds from "@/components/SideCorousalSlide";

export default {
  props: ["data"],
  data() {
    return {
      // slides: this.data,
      datas: undefined,
      countryid: this.$route.params.idname,
      image: undefined,
    };
  },
  components: {
    Adds,
    SideAdds,
  },
  mounted() {
    if (this.countryid != "" && this.countryid != undefined) {
      {
        {
          this.$route.params.emailid;
        }
      }
      this.axios
        .get(config.hostname + "api/countryinfo/Getbyid/" + this.countryid)
        .then((response) => {
          this.datas = response.data;
        });
    } else {
      this.$router.push({ path: "/guide" });
    }
  },
  methods: {
    getImage(pic) {
      this.image =   pic;
      return this.image;
    },
  },
};
</script>