<template>
<div class="col-12 col-lg-12">
<div class="main-card mb-3 card">
<div class="card-body">
<div class="app-main__inner">
    <form class="">
        <div v-for="d in newslist1" :key="d">
        <h3 style="color:#555f78">Scholorship</h3>
        <div class="position-relative row form-group"><label for="examplePassword" class="col-12 col-form-label">Title:<i style="color:red">*</i></label>
        <div class="col-sm-12"><input  v-model="d.title" id="tilt"  placeholder="Enter Title"  class="form-control" required></div>
        </div>
        <div class="position-relative row form-group"><label for="examplePassword" class="col-12 col-form-label">News Description:<i style="color:red">*</i></label>
        <div class="col-sm-12"><input v-model="d.shortdiscription"  id="des"  placeholder="Enter minimum 500 letter short description"  class="form-control" required></div>
        </div>
        <div class="position-relative row form-group">
        <div class="col-12 mt-2">
            <label for="exampleFile" class=" col-form-label">Select Countries:</label>
        </div>
        </div>
        <div class="position-relative row form-group">
        <div class="col-12 mt-2">

        <div id="chkmain"  class="multiselect">
                             <label><input id="chk1" type="checkbox" name="option[]" value="China">China</label>
                            <label><input id="chk2" type="checkbox" name="option[]" value="Austria">Austria</label>
                            <label><input id="chk3" type="checkbox" name="option[]" value="UK">UK</label>
                            <label><input id="chk4" type="checkbox" name="option[]" value="France">France</label>
                            <label><input id="chk5" type="checkbox" name="option[]" value="Germany">Germany</label>
                            <label><input id="chk6" type="checkbox" name="option[]" value="Canada">Canada</label>
                            </div>
        </div>
        </div>
        <div class="position-relative row form-group">
        <div class="position-relative row form-group col-12 mt-2"><label for="exampleFile" class="col-sm-12 col-form-label">Add article title logo(Size must not exceed 81px x 81px):</label>

        <div class="col-sm-10"><input id="upload-pic" name="file" ref="fileInput" accept="image/*" @change="onFilePicked($event)" type="file" class="form-control-file" required>
                        </div>
        </div>
        </div>

        <div class="position-relative row form-group">
        <div class="col-12 mt-2">
        <label for="exampleFile" class=" col-form-label">News Details:</label>
        <form method="post">
            <textarea v-model="d.detail" id="det"></textarea>
            </form>
        </div>
        </div>
    <div class="position-relative row form-group">
        
        <div class="col-12 mt-3 text-center">
        <button v-if="submitbtntrue" v-on:click.prevent="submit" class="btn btn-secondary">Submit</button>
        <button v-if="editbtntrue" v-on:click.prevent="updateOnClick(d.id)" class="btn btn-secondary">Update</button>
        </div>
    </div>
    </div>
</form>
    <div class="row">
        <div class="col-md-12">
        <div class="main-card mb-3 card">
        <div class="card-header">Manage your ScholarShip
            <div class="btn-actions-pane-right">
            </div>
        </div>
        <div class="table-responsive">
        <table class="align-middle mb-0 table table-borderless table-striped table-hover">
            <thead>
            <tr>
            <th class="text-center">Date</th>
            <th class="text-center">Title</th>
            <th class="text-center">Status</th>

            <th class="text-center">Edit</th>
            <th class="text-center">Delete</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="s in adslist" :key="s" >
            <td class="text-center text-muted">{{s.dates}}</td>
            <td class="text-center">{{s.title}}</td>
            <td class="text-center ">{{s.statuscheck}}</td>

            <td class="text-center">
            <button type="button" v-on:click.prevent="updatedClick(s.id)" id="PopoverCustomT-1" class="btn btn-primary btn-sm"><i class="fa fa-edit"> </i> Edit</button>
            </td>

            <td class="text-center">
            <button type="button" v-on:click.prevent="deleteClick(s.id)" id="PopoverCustomT-1" class="btn btn-primary btn-sm  bg-danger"><i class="pe-7s-trash"> </i> Delete</button>
            </td>
            </tr>
           
            </tbody>
        </table>
        </div>
        </div>
        </div>
</div>
</div>
</div>
</div>
</div>
</template>

<script>
import config from '@/../config'
import { create } from 'ipfs-http-client';
import { Buffer } from 'buffer';
export default {
    props:['emailsession','emails'],
    data () {
        return {
             file:"",
             key1value: "",
             arr: [],
             inc: 0,
            newslist1: [{"id":0,"title":"","shortdiscription":"","countries":"","detail":"","imageurl":"","email":"","dates":"","statuscheck":""}],
            adslist: undefined,
            submitbtntrue: true,
            editbtntrue: false,
            projectId: '2DBOP6lMpJJXMfPYTHHbUprAtAA',
            projectSecret : 'c47db02a18967d4b6b1758b83a53ffb8'
        }
       

    },mounted () {
      ////console.log(this.emails)
        this.axios.get(config.hostname+'api/scholarshipinfo/Getbyemail/' + this.emails )
              .then((response) => {
                this.adslist = response.data
                //////console.log(this.adslist)
                 })
    },
    methods:{
      async uploadToIpfs(file){
        
        const auth ='Basic ' + Buffer.from(this.projectId + ':' + this.projectSecret).toString('base64')

        const client = await create({
            host: 'ipfs.infura.io',
            port: 5001,
            protocol: 'https',
            headers: {
            authorization: auth
            }
        })
        const added = await client.add(file)
        const url = `https://abroadstudy.infura-ipfs.io/ipfs/${added.path}`
        return url
    }, 
        onChange(event) {
            this.key1value=event.target.value
            this.arr[this.inc] =  this.key1value
            this.inc++
            ////console.log(this.arr)
        },
        updatedClick (id) {
          this.submitbtntrue=false
          this.editbtntrue=true
        this.axios.get(config.hostname+'api/scholarshipinfo/Getbyid/' + id )
              .then((response) => {
                this.newslist1 = response.data
                ////console.log(this.adslist1)
                 })
        },
        async submit () {
          var url= await this.uploadToIpfs(this.file);
            //console.log("Url from Ads Uni");
            //console.log(url);
           var selected = new Array();
        //Reference the Table.
        var chkmain = document.getElementById("chkmain");
        //Reference all the CheckBoxes in Table.
        var chks = chkmain.getElementsByTagName("INPUT");
 
        // Loop and push the checked CheckBox value in Array.
        for (var i = 0; i < chks.length; i++) {
            if (chks[i].checked) {
                selected.push(chks[i].value);
            }
        }
        //Display the selected CheckBox values.
       // if (selected.length > 0) {
         //   alert("Selected values: " + selected.join(","));
     //   }
            this.tilt = document.getElementById('tilt').value
            this.des= document.getElementById('des').value
            this.det = document.getElementById('det').value
            ////console.log(this.tilt,this.emails,this.det,this.arr,this.des)
            
            const fd =new FormData()
            fd.append("title",this.tilt)
            fd.append("detail",this.det)
            fd.append("shortdiscription",this.des)
            fd.append("email",this.emails)
            fd.append("countries",selected.join(","))
            fd.append('imageurlbase64', url)
            ////console.log(this.file)
            if( this.file!="" && this.tilt!="" && this.det!="" && this.email!="")
            {
            
            this.axios.post(config.hostname+'api/scholarshipinfo/PosteventByemail/',fd,)
          .then((response) => {
            //Alert for record update Verification
            alert(response.data)
          })
            }
            else{ 
              alert("All Fields are Required!")
            }
          ////console.log(this.tilt,this.emails,this.det,this.arr,this.arr2)

        },
       async  updateOnClick (id){
          var url= await this.uploadToIpfs(this.file);
            //console.log("Url from Ads Uni");
            //console.log(url);
          var selected = new Array();
        //Reference the Table.
        var chkmain = document.getElementById("chkmain");
        //Reference all the CheckBoxes in Table.
        var chks = chkmain.getElementsByTagName("INPUT");
 
        // Loop and push the checked CheckBox value in Array.
        for (var i = 0; i < chks.length; i++) {
            if (chks[i].checked) {
                selected.push(chks[i].value);
            }
        }
        //Display the selected CheckBox values.
        //if (selected.length > 0) {
         //   alert("Selected values: " + selected.join(","));
       // }
            ////console.log(id)
            this.tilt = document.getElementById('tilt').value
            this.des= document.getElementById('des').value
            this.det = document.getElementById('det').value
            ////console.log(this.tilt,this.emails,this.det,this.arr,this.arr2)
            this.axios.put(config.hostname+'api/scholarshipinfo/Puteventbyid/', {
            id: id,
            Title: this.tilt,
            Detail: this.det,
            ShortDiscription: this.des,
            Email: this.emails,
            imageurl:"abc",
            Countries: selected.join(",")
        })
          .then((response) => {
            //Alert for record update Verification
            alert(response.data)
          })

         if(this.file != ""){
             const fd =new FormData()
            fd.append("email",this.emails)
            fd.append("id",id)
            fd.append('imageurlbase64', url)
            ////console.log(fd)
            this.axios.post(config.hostname+'api/scholarshipinfo/savepic/',fd)
            .then(res => {
              res
                //alert(res.data)
            })
          }


        },
        deleteClick (id) {
          if (!confirm('Are you sure?')) {
        return
      }
      this.axios.delete(config.hostname+'api/scholarshipinfo/' + id)
        .then((response) => {
          //Alert for record Delete Verification
          alert(response.data)
        })
      },
       onFilePicked (event) {
        this.file = event.target.files[0]
        }
    }
}
</script>