.<template>
  <h2 class="mt-5"><b>Embassies</b></h2>
	<p style="font-size: 16px">Pakistan maintains 86 embassies and high commissions abroad as well as 59 consulates and one other representation. The Pakistani capital Islamabad hosts 79 embassies and high commissions, and in addition there are 88 consulates and four other representations in Pakistan.</p>
	<div style="margin-top: 40px">
        <div class="container-fluid">
            <div class="row">
                <div class="col-sm-2 mt-2" v-for="s in embassydata" :key="s">
                    <div style="card;padding:10px 10px 10px 10px;border-radius:10px;height:100%;box-shadow:0 2px 5px 1px #403c4329;">
                        <div @click="ToSinglePage(s.id)" class="text-center">
                            <img :src="s.embassylogobase64" style="box-shadow:0 2px 5px 1px #403c4329;width:100px;height:75px;" class="" >
                            <br><label class="mt-4" style="color:black">{{s.embassyname}}</label>
                        </div> 
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
//import config from '@/../../config'

export default {
    props: ['embassydata'],
      data(){
          return{
            image:undefined
            

          }
      },
      
      methods:{
          
          getImage (pic) {     
                this.image =   pic
                    return this.image           
                },
      
      ToSinglePage (cname) {
        // this.$router.push({path: '/ConsultantPage', params: { username: email }});
        // this.$router.push({name:'ConsultantPage', params: { param: email } })
        this.$router.push({name: '/embassypage', params: { idname: cname}});
      }
  }
}
</script>

