.<template>
  <div class="bg-overlay" style="">
    <div class="container-fluid p-5">
      <h3 align="center">Study Abroad News Page</h3>
      <!-- <div class="row mt-4">
                
                    <div class="col-md-12">
                        <div class="position-relative form-group"><input type="text" id="exampleCustomSelect" name="customSelect" class="custom-input-1" placeholder="Search Here..">
                    </div>
                    </div>
            </div>
            <div class="text-center mt-2">
                <button class="mb-2 mr-2 btn btn-primary"><i class="pe-7s-search"> </i>  Spearch</button>
            </div> -->
    </div>
  </div>
  <Adds :slides="data" />
  <div class="container-fluid" style="padding: 50px">
    <div class="row">
      <div v-for="d in datas" :key="d" class="col-sm-9">
        <div class="card" style="padding: 20px">
          <div class="single-blog mb-5 wow fadeInUp animated">
            <img
              :src="d.imageurlbase64"
              style="border-radius: 100%; height: auto; width: 100%"
              class=""
            />
            <div align="center" class="mt-2">
              <h4>{{ d.title }}</h4>
            </div>
          </div>
          <div align="left">
            <div align="left" class="col-sm-12">
              <p>{{ d.shortdiscription }}</p>
              <p>{{ d.detail }}</p>
            </div>
            <h5 class="mt-3">Services Countries :</h5>
            <div align="left" class="col-sm-12">
              {{ d.countries }}
            </div>

            <h5 class="mt-3">Contact Details :</h5>
            <div align="left" class="col-sm-12">
              <div class="row">
                <div class="col-sm-3">
                  <h6>Email :</h6>
                </div>
                <div class="col-sm-9">
                  <label>{{ d.email }}</label>
                </div>
              </div>

              <div class="mt-2">
                <div id="gmap-example"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <SideAdds :slides="data" />
      </div>
    </div>
  </div>
</template>
<script>
import config from "@/../config";
//import SliderImg from '@/SliderImages.json'
import Adds from "@/components/CorousalSlide";
import SideAdds from "@/components/SideCorousalSlide";

export default {
  props: ["data"],
  data() {
    return {
      datas: undefined,
      id: this.$route.params.emailid,
      image: undefined,
      ratingValue: "",
      mainEmail: this.$route.params.mainemail,
    };
  },
  components: {
    Adds,
    SideAdds,
  },
  async mounted() {
    if (this.id != "" && this.id != undefined) {
      {
        {
          this.$route.params.mainemail;
        }
      }
      {
        {
          this.$route.params.emailid;
        }
      }
      await this.axios
        .get(config.hostname + "api/eventsinfo/Getbyid/" + this.id)
        .then((response) => {
          this.datas = response.data;
          ////console.log(this.data)
        });
      this.axios
        .put(config.hostname + "api/ViewsCounter/NewsSinglepageviewsupdate/", {
          Email: this.mainEmail,
        })
        .then((response) => {
          response;
          ////console.log(response.data)
        });
    } else {
      this.$router.push({ path: "/news" });
    }
  },
  methods: {
    getImage(pic) {
      this.image =   pic;
      return this.image;
    },
  },
};
</script>