<template>
    <div class="card" v-for="s in eventinfodata" :key="s">
        <div >
            <div class="single-blog mb-5 wow fadeInUp animated">
            <div class="single-blog-image">
                <img class="img-fluid mb-3 h-50 w-50 center" :src="getImage1(s.imageurlbase64)" >
            </div>
            <div class="single-blog-dec"> 
                <div @click="ToSinglePage(s.id,s.email)">
               <h4>{{s.title}}</h4>
                <span class="post-date"><i class="icofont icofont-calendar"></i> {{s.dates}}</span>
                <span class="allpost-cata"><i class="icofont icofont-ui-tag"></i>{{s.countries}}</span>
                <p> 
                    {{s.detail}}               
                 </p>
                <div class="single-blog-admin">
                    
                    <div class="single-blog-admin-info">
                        <p>By: <span>{{s.email}}</span></p>
                    </div>
                </div>
               </div>
            </div>
            <br>
        </div>
        </div>
        
	</div>	
</template>
<script>
// import config from '@/../../config'

  export default {
      props: ['eventinfodata'],
      data(){
          return{
            image1:undefined,

          }
      },
      
      methods:{
          
          getImage1 (pic) {     
                this.image1 =   pic
                    return this.image1           
                },
      
      ToSinglePage (id,email) {
        this.$router.push({name: '/newsPage', params: { emailid: id, mainemail:email}});
      }  
  }
}
</script>